import React, { useEffect } from 'react';
import Cardlist from '../components/Cardlist';
import ProgressCircle from '../components/ProgressCircle';

import PrimarySearchAppBar from '../components/PrimarySearchAppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Button from '@material-ui/core/Button';
import './App.css';
import { useGlobalContext } from './GlobalContext';




function App() {
  const { handleCreepClick, creepCount, data } = useGlobalContext();

  const getDataFromUrl = (count) => {
      const url = 'https://randomuser.me/api/?results=' + count;
      // get the data from the api and convert into js object for use in this app
      fetch(url).then((response)=> response.json())
      .then((thedata)=>{
          // setData( thedata['results']);
      });
  }




  // Similar to componentDidMount and componentDidUpdate:
  useEffect(() => {
    // Update the document title using the browser API
    document.title = `You are creeping ${creepCount} people`;
  });

  useEffect(() => {
      getDataFromUrl(15);
      // fetchData();

  },[]);





  // console.log('apiData.length',apiData.length);
  // if no data from the api display a validate credentials screeen.
  if (data.length === 0) {
  console.log('nodata');
  return (
    <>
        <CssBaseline />
        {/* <Container maxWidth="lg"> */}
        <PrimarySearchAppBar></PrimarySearchAppBar>
        <div className="buffer"></div>

        <h1 className='center'>LOADING</h1>
        <div className='center'>
          <ProgressCircle></ProgressCircle>
       </div>
      {/* </Container> */}
    </>
  )
} else {
    return (
    <>
        <CssBaseline />
        {/* <Container maxWidth="lg"> */}
        <PrimarySearchAppBar></PrimarySearchAppBar>
        <div className="buffer"></div>
        <div className='center'>
        
        <Button className='center' onClick={handleCreepClick} variant="contained">Click here to creep 50 more people <br></br> You have creeped {creepCount} times!!</Button> </div>
        <Cardlist key='cardlistkey1234' JSONdataArray={data}></Cardlist>
        <div className="footer center">Data from {'\u00A0'}<a href="https://randomuser.me/"> Random User API </a></div>
        <div className="footer center">Made with {'\u00A0'}<a href="https://material-ui.com/"> React and Material.ui </a></div>
      {/* </Container> */}
    </>
  );
}



}


export default App;