import React from 'react';
import SnackBar from './SnackBar'
import PrimarySearchAppBar from './PrimarySearchAppBar'
// import { ReferenceDataContext, ReferenceDataContextProvider } from "../components/ReferenceDataContext"
import CssBaseline from '@material-ui/core/CssBaseline';
import Button from '@material-ui/core/Button';
// import Container from '@material-ui/core/Container';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';



const useStyles = makeStyles((theme) => ({
  button: {
    '& > *': {
      margin: theme.spacing(7),
    },
  },
}));


const Validate = () => {

  const classes = useStyles();

    return (
    <>
        <CssBaseline />
        <PrimarySearchAppBar></PrimarySearchAppBar>
        <div className="buffer"></div>

        <h1>Welcome to Stalk-Tok</h1>
        <SnackBar />

        <div className='center'>
        <Link className='link' to='/'>
        <Button className={classes.button}  variant="contained">Insta-Creep <br></br> Click Now to Load 50 People  </Button></Link> </div>
        <div className="footer center">Data from {'\u00A0'}<a href="https://randomuser.me/"> Random User API </a></div>
        <div className="footer center">Made with {'\u00A0'}<a href="https://material-ui.com/"> React and Material.ui </a></div>
    </>
  )
}

export default Validate;