import React from 'react';
import Usercard from './Usercard'
// material ui
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
// import { useContext } from 'react';
import { useGlobalContext } from '../containers/GlobalContext';
// import { Link } from 'react-router-dom';
// import { useGlobalContext } from '../containers/GlobalContext';

import './CardList.css';





const useStyles = makeStyles((theme) => ({
  grid: {
    flexGrow: 1
  }
}));

function Cardlist(props){

    // gain access to currentSearch in GlobalContext
    const { currentSearch } = useGlobalContext()




    // console.log('CardListprops', props.JSONdataArray);
    const {JSONdataArray} = props;
    const classes = useStyles();

    const filteredSearch = JSONdataArray.results.filter(user => {
      return user.name.first.toLowerCase().includes(currentSearch.toLowerCase());
    })
    if (filteredSearch.length === 0) {
      return(<>
        <h3>User can be found that fits your search of "{currentSearch}". <br/> Try the Creep 50 more button</h3>
      </>) 
      
    } else {

    return(<>
       <Grid justify="center" container className={classes.grid}>
           { 
               filteredSearch.map((JSONdataArrayToCard, index)=>{
                return (<div key={JSONdataArrayToCard.login.uuid} >
                  <Grid  item xs>
                      
                        <Usercard key={'usercard-' + JSONdataArrayToCard.login.uuid} {...JSONdataArrayToCard}/>

                  </Grid>
                  </div>)
               })
           }
       </Grid>
    </>) 

    }

};

export default Cardlist;



