import React, { useContext, useReducer, useEffect } from 'react';
import reducer from './reducer';
import stalktoksfx1 from '../components/stalktoksfx.mp3';
import { useFetch } from '../components/useFetch';


const url = 'https://randomuser.me/api/?results=50';



const GlobalContext = React.createContext()

const initialState = {
  loading: false,
  data: [],
  creepCount: 0,
  currentSearch: '',
};


// This context provider is passed to any component requiring the context
// children is key word
const GlobalContextProvider = ({ children }) => {
  // use reducer for dispatch
  const [state, dispatch] = useReducer(reducer, initialState)
  
  // load audio file on component load
  const stalktoksfx = new Audio(stalktoksfx1);

  useEffect(() => {
    console.log('loadsoundfx');
    stalktoksfx.load();
  },[])



  // takes data from useFetch custom hook, returns and obj with {loading:true, data:[data]}
  // loads initial data
  const { data } = useFetch(url)
  initialState.data = data




  const searchChange = (currentSearch) => {
    dispatch({ type: 'SEARCH_CHANGE', payload: currentSearch })
  }

  const fetchData = () => {
        // load a new batch of data
        // get the data from the api and convert into js object for use in this app
        fetch(url).then((response)=> response.json())
        .then((thedata)=>{
            // console.log('FETCH_DATA', thedata);
            dispatch({ type: 'FETCH_DATA', payload: thedata })
        });
  }

  const handleCreepClick = () => {
    stalktoksfx.play();
    fetchData();
    console.log('playsound');
    dispatch({ type: 'HANDLE_CREEPCLICK' });

  }



  return (
    <GlobalContext.Provider
      value={{
        ...state,
        
        searchChange,
        handleCreepClick,
        fetchData,
        
      }}
    >
      {children}
    </GlobalContext.Provider>
  );
};

// make sure use
export const useGlobalContext = () => {
  return useContext(GlobalContext)
}

export { GlobalContext, GlobalContextProvider }