import React from 'react';
import { useGlobalContext } from '../containers/GlobalContext';
import { useParams } from 'react-router-dom';
import { Link } from 'react-router-dom';
import './ViewProfile.css';

// for materialUI Card
import CssBaseline from '@material-ui/core/CssBaseline';

import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';

// material ui styles
const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 1000,
  },
  media: {
    height: 0,
    paddingTop: '88%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
}));


// the CARD component

const ViewProfile = () => {
    // get data from global context
    const { loading, data } = useGlobalContext();
    // get id for this card
    const { id } = useParams();
    console.log('id', id);

    // material ui class for style
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);

    const handleExpandClick = () => {
        setExpanded(!expanded);
    };

  if (loading) {
    console.log('loading ', data);
      return (
    <div>loading

    </div>
  );

  } else {
    let thisPerson = data.results.filter(obj => {
      // console.log('obj.login.uuid',obj.login.uuid);
      return obj.login.uuid === id;
    })
    console.log('id, filtered person',id, thisPerson[0]);
    const {login, cell, email, gender, name, picture, location} = thisPerson[0];

    return (<>
    
    <CssBaseline />

      <div className='center profileCard'>
        {/* <h1>{name.first} {name.last} </h1> */}

        <Card  className={classes.root}>
        <CardHeader
            avatar={
            <Avatar className={classes.large} alt={name.first} src={picture.large} />
            }
            action={
            <IconButton aria-label="settings">
                <MoreVertIcon />
            </IconButton>
            }
            title={`${name.first} ${name.last}`}
            subheader={`${location.country}. ${location.state}`}
        />
        <CardMedia
            className={classes.media}
            image={picture.large}
            title="Paella dish"
        />



        <CardContent>
            <Typography variant="body2" color="textSecondary" component="p">
            {`${name.first} ${name.last} lives at ${location.street.number}, ${location.street.name} in ${location.city}. `}
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
            {`Stalk-Tok last tracked this ${gender} at these coordinates: `}
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
            {`(${location.coordinates.longitude}, ${location.coordinates.latitude})`}
            </Typography>
        </CardContent>
        <CardActions disableSpacing>
            <IconButton aria-label="add to favorites">
            <FavoriteIcon />
            </IconButton>
            <IconButton aria-label="share">
            <ShareIcon />
            </IconButton>
            <IconButton
            className={clsx(classes.expand, {
                [classes.expandOpen]: expanded,
            })}
            onClick={handleExpandClick}
            aria-expanded={expanded}
            aria-label="show more"
            >
            <ExpandMoreIcon />
            </IconButton>
        </CardActions>
        <Collapse in={expanded} timeout="auto" unmountOnExit>
            <CardContent>
            <Typography paragraph>Phone Number: {cell}</Typography>
            <Typography paragraph>Email: {email}</Typography>
            <Typography paragraph>Login Name: {login.username}</Typography>
            <Typography paragraph>Known Passwords: '{login.password}'</Typography>
            <Typography>
                Please use Stalk-Tok responsibly
            </Typography>
            
            </CardContent>
        </Collapse>
        </Card>


      </div>
              <Link to='/' className='profileBack link'>
          Back To People
        </Link>
  </>);

  }

};

export default ViewProfile;
